      position: absolute;
      width: 13px;
      height: 3px;
      background-color: #fff;
      content: "";
      top: -9px; }
    .btn11 .icon-right:after {
      -webkit-transition-duration: 0.4s;
      transition-duration: 0.4s;
      position: absolute;
      width: 13px;
      height: 3px;
      background-color: #fff;
      content: "";
      top: 9px; }
  .btn11.open .icon-left {
